.success-icon {
  position: absolute;
  top: 13vw;
  left: 50%;
  transform: translateX(-50%);
}
.success-icon img {
  width: 20vw;
  height: 20vw;
}
.success-icon .success-text {
  font-size: 4vw;
  color: #0080FF;
  text-align: center;
  margin-top: 2vw;
}

.success-tools {
  display: flex;
  width: 100%;
  justify-content: space-between;
  position: absolute;
  padding: 0 4vw;
  bottom: 4vw;
}
.success-tools a {
  text-align: center;
  line-height: 12vw;
  width: 44vw;
  height: 12vw;
  font-size: 4vw;
  border-radius: 2vw;
}
.success-tools .pos-manage {
  color: #0080FF;
  border: 0.12vw solid #0080FF;
}
.success-tools .pub-position {
  color: #FFFFFF;
  background-color: #0080FF;
}