.app-main-layout .app-header-layout {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 4;
  box-sizing: border-box;
  border-bottom: 1px solid #efefef;
  background: #FFFFFF;
}
.app-main-layout .app-header-layout .app-header-inner {
  height: 72px;
  padding: 0.5rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.app-main-layout .app-header-layout .app-header-inner .nav-left {
  flex: 1 1;
}
.app-main-layout .app-header-layout .app-header-inner .nav-left .app-title {
  cursor: pointer;
  color: #333333;
  font-size: 25px;
  text-decoration: none;
}
.app-main-layout .app-header-layout .app-header-inner .nav-right {
  margin-left: 1rem;
  display: flex;
  align-items: center;
}
.app-main-layout .app-body-layout {
  padding-top: 72px;
  flex: 1 1;
  z-index: 0;
}
.app-main-layout .app-body-layout .app-body-inner {
  display: flex;
}
.app-main-layout .app-body-layout .app-body-inner .app-sider {
  padding-top: 2rem;
  width: 220px;
  flex: none;
}
.app-main-layout .app-body-layout .app-body-inner .app-sider .sep-line {
  width: 200px;
}
.app-main-layout .app-body-layout .app-body-inner .app-sider a {
  width: 200px;
  display: block;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.875rem;
  color: #454545;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}
.app-main-layout .app-body-layout .app-body-inner .app-sider a.active, .app-main-layout .app-body-layout .app-body-inner .app-sider a:hover {
  color: #0080FF;
}
.app-main-layout .app-body-layout .app-body-inner .app-content {
  padding: 16px;
  flex: 1 1;
  background-color: #F6F6FA;
}
.app-main-layout .app-footer-layout {
  z-index: 1;
}