.position-item {
  display: flex;
  justify-content: space-between;
  padding: 4vw 4vw 2.6vw;
  width: 92vw;
  height: 39.7vw;
  border-radius: 2.6vw;
  background-color: #FFFFFF;
}
.position-item .item-left .item-name {
  color: #333333;
  font-size: 4.5vw;
  font-weight: bold;
}
.position-item .item-left .item-tag {
  margin-top: 4vw;
}
.position-item .item-left .item-tag span {
  font-size: 3.2vw;
  margin-right: 2.6vw;
  background-color: #F5F5F5;
  color: #666666;
  padding: 1vw 3.5vw;
  border-radius: 1.3vw;
}
.position-item .item-left .item-company {
  margin-top: 2vw;
}
.position-item .item-left .item-company img {
  width: 4.8vw;
  height: 4.8vw;
}
.position-item .item-left .item-company .company-text {
  margin-left: 0.1vw;
  font-size: 3.5vw;
  color: #333333;
}
.position-item .item-left .recruiterInfo {
  margin-top: 2.6vw;
}
.position-item .item-left .recruiterInfo img {
  width: 6.6vw;
  height: 6.6vw;
  border-radius: 50%;
}
.position-item .item-left .recruiterInfo .recruiter-name {
  margin-left: 2.6vw;
  color: #000000;
  font-size: 3.2vw;
  font-weight: 400;
  font-family: PingFang SC;
  height: 6.6vw;
  line-height: 6.6vw;
}
.position-item .item-right .item-salary {
  text-align: right;
  color: #FF9900;
  font-size: 4.5vw;
}
.position-item .item-right .item-isPub {
  text-align: right;
  font-size: 4.5vw;
  color: #ACD5FF;
  margin-top: 6.3vw;
}
.position-item .item-right .item-address {
  color: #999999;
  font-size: 3.2vw;
  margin-top: 8vw;
}