.position-manage {
  font-family: PingFang SC;
  margin-top: 5.3vw;
}
.position-manage .position-item {
  margin: 4vw auto 0;
}
.position-manage .publish-position {
  width: 60vw;
  height: 11.7vw;
  border-radius: 2vw;
  color: #FFFFFF;
  background-color: #0080FF;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 8vw;
  text-align: center;
  line-height: 11.7vw;
}